/* Modern Dark Theme for LAN Relay Dashboard */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(135deg, #0f172a 0%, #1e293b 100%);
  color: #e2e8f0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
  background-color: #334155;
  color: #e2e8f0;
  padding: 2px 4px;
  border-radius: 4px;
}

/* Base colors - Dark Theme */
.min-h-screen { min-height: 100vh; }
.bg-gray-100 { background: linear-gradient(135deg, #0f172a 0%, #1e293b 100%); }
.bg-white { 
  background: rgba(30, 41, 59, 0.95);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(51, 65, 85, 0.3);
}
.bg-blue-600 { 
  background: linear-gradient(135deg, #3b82f6 0%, #6366f1 100%);
  box-shadow: 0 4px 14px 0 rgba(59, 130, 246, 0.25);
}
.bg-blue-700 { 
  background: linear-gradient(135deg, #2563eb 0%, #4f46e5 100%);
  box-shadow: 0 4px 14px 0 rgba(37, 99, 235, 0.35);
}
.bg-red-600 { 
  background: linear-gradient(135deg, #dc2626 0%, #e11d48 100%);
  box-shadow: 0 4px 14px 0 rgba(220, 38, 38, 0.25);
}
.bg-red-700 { 
  background: linear-gradient(135deg, #b91c1c 0%, #be185d 100%);
  box-shadow: 0 4px 14px 0 rgba(185, 28, 28, 0.35);
}
.bg-green-600 { 
  background: linear-gradient(135deg, #16a34a 0%, #059669 100%);
  box-shadow: 0 4px 14px 0 rgba(22, 163, 74, 0.25);
}
.bg-green-700 { 
  background: linear-gradient(135deg, #15803d 0%, #047857 100%);
  box-shadow: 0 4px 14px 0 rgba(21, 128, 61, 0.35);
}

/* Status card colors */
.bg-green-100 { background: rgba(34, 197, 94, 0.1); border: 1px solid rgba(34, 197, 94, 0.2); }
.bg-blue-100 { background: rgba(59, 130, 246, 0.1); border: 1px solid rgba(59, 130, 246, 0.2); }
.bg-yellow-100 { background: rgba(245, 158, 11, 0.1); border: 1px solid rgba(245, 158, 11, 0.2); }
.bg-red-100 { background: rgba(239, 68, 68, 0.1); border: 1px solid rgba(239, 68, 68, 0.2); }
.bg-gray-50 { background: rgba(30, 41, 59, 0.5); }

/* Text colors - Dark Theme */
.text-white { color: #ffffff; }
.text-gray-900 { color: #f1f5f9; }
.text-gray-600 { color: #cbd5e1; }
.text-gray-500 { color: #94a3b8; }
.text-blue-600 { color: #60a5fa; }
.text-green-600 { color: #4ade80; }
.text-red-600 { color: #f87171; }
.text-yellow-600 { color: #fbbf24; }
.text-orange-600 { color: #fb923c; }

/* Enhanced styling */
.p-4 { padding: 1rem; }
.p-6 { padding: 1.5rem; }
.p-8 { padding: 2rem; }
.px-4 { padding-left: 1rem; padding-right: 1rem; }
.px-6 { padding-left: 1.5rem; padding-right: 1.5rem; }
.py-2 { padding-top: 0.5rem; padding-bottom: 0.5rem; }
.py-4 { padding-top: 1rem; padding-bottom: 1rem; }
.py-6 { padding-top: 1.5rem; padding-bottom: 1.5rem; }
.py-8 { padding-top: 2rem; padding-bottom: 2rem; }

.m-4 { margin: 1rem; }
.mb-1 { margin-bottom: 0.25rem; }
.mb-2 { margin-bottom: 0.5rem; }
.mb-4 { margin-bottom: 1rem; }
.mb-8 { margin-bottom: 2rem; }
.mt-1 { margin-top: 0.25rem; }
.mt-2 { margin-top: 0.5rem; }
.mt-4 { margin-top: 1rem; }
.mt-8 { margin-top: 2rem; }
.ml-2 { margin-left: 0.5rem; }
.mr-2 { margin-right: 0.5rem; }

.rounded-lg { border-radius: 0.75rem; }
.rounded-full { border-radius: 9999px; }
.rounded { border-radius: 0.5rem; }

/* Modern shadows and effects */
.shadow { 
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.3), 0 2px 4px -1px rgba(0, 0, 0, 0.2);
}
.shadow-lg { 
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.4), 0 4px 6px -2px rgba(0, 0, 0, 0.3);
}
.shadow-xl { 
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.4), 0 10px 10px -5px rgba(0, 0, 0, 0.2);
}

/* Borders */
.border { border: 1px solid rgba(51, 65, 85, 0.3); }
.border-2 { border: 2px solid rgba(51, 65, 85, 0.3); }
.border-gray-200 { border-color: rgba(51, 65, 85, 0.3); }
.border-green-200 { border-color: rgba(34, 197, 94, 0.3); }
.border-blue-200 { border-color: rgba(59, 130, 246, 0.3); }
.border-red-200 { border-color: rgba(239, 68, 68, 0.3); }
.border-yellow-200 { border-color: rgba(245, 158, 11, 0.3); }

/* Layout */
.flex { display: flex; }
.grid { display: grid; }
.grid-cols-1 { grid-template-columns: repeat(1, minmax(0, 1fr)); }
.grid-cols-4 { grid-template-columns: repeat(4, minmax(0, 1fr)); }
.gap-6 { gap: 1.5rem; }

.items-center { align-items: center; }
.justify-between { justify-content: space-between; }
.text-center { text-align: center; }

/* Typography */
.text-xs { font-size: 0.75rem; }
.text-sm { font-size: 0.875rem; }
.text-lg { font-size: 1.125rem; }
.text-xl { font-size: 1.25rem; }
.text-2xl { font-size: 1.5rem; }
.text-3xl { font-size: 1.875rem; }

.font-bold { font-weight: bold; }
.font-medium { font-weight: 500; }
.font-semibold { font-weight: 600; }

/* Sizing */
.max-w-7xl { max-width: 80rem; }
.max-w-md { max-width: 28rem; }
.max-w-xs { max-width: 20rem; }
.w-full { width: 100%; }
.w-8 { width: 2rem; }
.w-5 { width: 1.25rem; }
.h-8 { height: 2rem; }
.h-5 { height: 1.25rem; }
.h-4 { height: 1rem; }

.mx-auto { margin-left: auto; margin-right: auto; }
.space-x-2 > * + * { margin-left: 0.5rem; }
.space-x-3 > * + * { margin-left: 0.75rem; }
.space-y-3 > * + * { margin-top: 0.75rem; }

.overflow-hidden { overflow: hidden; }
.overflow-x-auto { overflow-x: auto; }

.whitespace-nowrap { white-space: nowrap; }
.truncate { overflow: hidden; text-overflow: ellipsis; white-space: nowrap; }

/* Modern transitions and interactions */
.transition-all { 
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1); 
}
.transition-colors { 
  transition: color 0.3s ease, background-color 0.3s ease, border-color 0.3s ease; 
}

.hover\:bg-blue-700:hover { 
  background: linear-gradient(135deg, #2563eb 0%, #4f46e5 100%);
  transform: translateY(-1px);
  box-shadow: 0 6px 20px 0 rgba(37, 99, 235, 0.4);
}
.hover\:bg-red-700:hover { 
  background: linear-gradient(135deg, #b91c1c 0%, #be185d 100%);
  transform: translateY(-1px);
  box-shadow: 0 6px 20px 0 rgba(185, 28, 28, 0.4);
}
.hover\:bg-green-700:hover { 
  background: linear-gradient(135deg, #15803d 0%, #047857 100%);
  transform: translateY(-1px);
  box-shadow: 0 6px 20px 0 rgba(21, 128, 61, 0.4);
}
.hover\:bg-gray-50:hover { background: rgba(30, 41, 59, 0.7); }

.cursor-pointer { cursor: pointer; }

.inline-flex { display: inline-flex; }
.divide-y > * + * { border-top: 1px solid rgba(51, 65, 85, 0.3); }
.divide-gray-200 > * + * { border-color: rgba(51, 65, 85, 0.3); }

.min-w-full { min-width: 100%; }

.uppercase { text-transform: uppercase; }
.tracking-wider { letter-spacing: 0.05em; }

/* Enhanced animations */
.animate-pulse { animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite; }
.animate-fade-in { animation: fadeIn 0.5s ease-in-out; }

@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.6; }
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(10px); }
  to { opacity: 1; transform: translateY(0); }
}

/* Table styles - Dark theme */
table { 
  border-collapse: collapse; 
  width: 100%; 
  background: rgba(30, 41, 59, 0.95);
}
th, td { 
  text-align: left; 
  border-bottom: 1px solid rgba(51, 65, 85, 0.3);
}
th {
  background: rgba(15, 23, 42, 0.8);
  color: #cbd5e1;
  font-weight: 600;
}

/* Input and form styles */
.input-field {
  background: rgba(30, 41, 59, 0.8);
  border: 1px solid rgba(51, 65, 85, 0.5);
  color: #e2e8f0;
  border-radius: 0.5rem;
  padding: 0.75rem;
  transition: all 0.3s ease;
}

.input-field:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.input-field::placeholder {
  color: #64748b;
}

/* Modal styles */
.modal-backdrop {
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(4px);
}

.modal-content {
  background: rgba(30, 41, 59, 0.98);
  border: 1px solid rgba(51, 65, 85, 0.3);
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.7);
}

/* Status indicators */
.status-online {
  background: linear-gradient(135deg, #10b981 0%, #059669 100%);
  color: white;
}

.status-offline {
  background: linear-gradient(135deg, #ef4444 0%, #dc2626 100%);
  color: white;
}

.status-warning {
  background: linear-gradient(135deg, #f59e0b 0%, #d97706 100%);
  color: white;
}

/* Responsive design */
@media (min-width: 768px) {
  .md\:grid-cols-2 { grid-template-columns: repeat(2, minmax(0, 1fr)); }
  .md\:flex { display: flex; }
  .md\:block { display: block; }
}

@media (min-width: 1024px) {
  .lg\:grid-cols-4 { grid-template-columns: repeat(4, minmax(0, 1fr)); }
  .lg\:px-8 { padding-left: 2rem; padding-right: 2rem; }
}

@media (min-width: 640px) {
  .sm\:px-6 { padding-left: 1.5rem; padding-right: 1.5rem; }
}

/* Glass morphism effect for cards */
.glass-card {
  background: rgba(30, 41, 59, 0.9);
  backdrop-filter: blur(16px);
  border: 1px solid rgba(51, 65, 85, 0.2);
  border-radius: 1rem;
  box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.37);
}

/* Example and Feature Styles */
.example-list {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  margin-top: 0.5rem;
}

.example-item {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  padding: 0.75rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  transition: all 0.2s ease;
}

.example-item:hover {
  background: rgba(255, 255, 255, 0.08);
  transform: translateY(-1px);
}

.example-label {
  font-size: 0.875rem;
  font-weight: 600;
  color: #60a5fa;
  margin-bottom: 0.25rem;
}

.example-item code {
  font-size: 0.75rem;
  background: rgba(0, 0, 0, 0.3);
  padding: 0.375rem 0.5rem;
  border-radius: 4px;
  word-break: break-all;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.features ul {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  margin-top: 0.5rem;
  list-style: none;
  padding: 0;
}

.features li {
  padding: 0.5rem;
  background: rgba(255, 255, 255, 0.03);
  border-radius: 6px;
  border-left: 3px solid #60a5fa;
  font-size: 0.875rem;
}

.tech-note {
  margin-top: 1rem;
  padding: 0.75rem;
  background: linear-gradient(135deg, rgba(59, 130, 246, 0.1), rgba(147, 51, 234, 0.1));
  border: 1px solid rgba(59, 130, 246, 0.2);
  border-radius: 8px;
}

.tech-note p {
  margin: 0;
  font-size: 0.875rem;
  color: #cbd5e1;
}

.tech-note code {
  background: rgba(0, 0, 0, 0.3);
  padding: 0.125rem 0.375rem;
  border-radius: 3px;
  font-size: 0.8rem;
}
